import React, { Component} from 'react';

export class MediaScreenshotMessage extends Component {
    render() {
        return (
            <div className="media_screenshot">
                {
                    this.props.sender?
                        <div className="message__sender">
                            {this.props.sender}
                        </div>
                        :
                        <div></div>
                }

                <img className="media_screenshot__img" src={this.props.path} />

                {
                    this.props.sender?
                        <div className="message__sender">
                            {this.props.description}
                        </div>
                        :
                        <div></div>
                }
            </div>
        )
    }
}