import React, { Component} from 'react';
import {DirtyHandsScreen} from "../usecases/DirtyHandsScreen";
import {EyeCureScreen} from "../usecases/EyeCureScreen";
import {MusicAdminScreen} from "../usecases/MusicAdminScreen";
import strings from "../Localisation";
import idGenerator from "react-id-generator";
import {ButtonLink} from "../interactive/ButtonLink";
import {BookReaderScreen} from "../usecases/BookReaderScreen";
import {TakePhotoScreen} from "../usecases/TakePhotoScreen";
import {SmartGlassesScreen} from "../usecases/SmartGlassesScreen";
import {StartingBuildScreen} from "../usecases/StartingBuildScreen";
import {TurnOffLightScreen} from "../usecases/TurnOffLightScreen";
import {IFTTTIntegrationScreen} from "../usecases/IFTTTIntegrationScreen";
import {ShowingPresentationScreen} from "../usecases/ShowingPresentationScreen";
import {MonitorTicsScreen} from "../usecases/MonitorTicsScreen";
import {MonitorProductivityScreen} from "../usecases/MonitorProductivityScreen";
import {MonitorStressScreen} from "../usecases/MonitorStressScreen";
import {MonitorMoodScreen} from "../usecases/MonitorMoodScreen";
import {CountSmilesScreen} from "../usecases/CountSmilesScreen";
import {CountBitesAndSipsScreen} from "../usecases/CountBitesAndSipsScreen";

export class UsecasesScrollingScreen extends Component {
    local_index = 0;

    constructor(props) {
        super(props);
        this.state = {};

        this.count = 16;
        this.index = 0;
        this.local_index = 0;

        this.button_id__previous = idGenerator();
        this.button_id__next = idGenerator();
    }

    componentDidMount() {
        let button_previous = document.getElementById(this.button_id__previous);
        let button_next = document.getElementById(this.button_id__next);
        let link = this;

        button_previous.addEventListener("click", function(event) {
            event.preventDefault();

            link.index -= 1;
            if (link.index < 0) {
                link.index = link.count + link.index;
            }

            link.setState({});
        });

        button_next.addEventListener("click", function(event) {
            event.preventDefault();

            link.index += 1;
            link.setState({});
        });
    }

    renderingComponent(component, context) {
        context.index = context.index % context.count;
        context.local_index = context.local_index % context.count;

        console.log(context.index, context.count, context.local_index);

        const is_current = context.index === context.local_index;
        context.local_index += 1;

        return is_current? component : <div></div>;
    }

    returnDefaultSequence() {
        return (
            <div>
                { this.renderingComponent(<DirtyHandsScreen />, this) }
                { this.renderingComponent(<EyeCureScreen />, this) }
                { this.renderingComponent(<MonitorProductivityScreen />, this) }
                { this.renderingComponent(<MonitorStressScreen />, this) }
                { this.renderingComponent(<MusicAdminScreen />, this) }
                { this.renderingComponent(<BookReaderScreen />, this) }
                { this.renderingComponent(<ShowingPresentationScreen />, this) }
                { this.renderingComponent(<SmartGlassesScreen />, this) }
                { this.renderingComponent(<IFTTTIntegrationScreen />, this) }
                { this.renderingComponent(<StartingBuildScreen />, this) }
                { this.renderingComponent(<TurnOffLightScreen />, this) }
                { this.renderingComponent(<TakePhotoScreen />, this) }
                { this.renderingComponent(<MonitorMoodScreen />, this) }
                { this.renderingComponent(<MonitorTicsScreen />, this) }
                { this.renderingComponent(<CountSmilesScreen />, this) }
                { this.renderingComponent(<CountBitesAndSipsScreen />, this) }
            </div>
        )
    }

    returnHealthSequence() {
        return (
            <div>
                { this.renderingComponent(<EyeCureScreen />, this) }
                { this.renderingComponent(<MonitorProductivityScreen />, this) }
                { this.renderingComponent(<MonitorStressScreen />, this) }
                { this.renderingComponent(<MonitorMoodScreen />, this) }
                { this.renderingComponent(<MonitorTicsScreen />, this) }
                { this.renderingComponent(<CountSmilesScreen />, this) }
                { this.renderingComponent(<CountBitesAndSipsScreen />, this) }
                { this.renderingComponent(<DirtyHandsScreen />, this) }
                { this.renderingComponent(<MusicAdminScreen />, this) }
                { this.renderingComponent(<BookReaderScreen />, this) }
                { this.renderingComponent(<ShowingPresentationScreen />, this) }
                { this.renderingComponent(<SmartGlassesScreen />, this) }
                { this.renderingComponent(<IFTTTIntegrationScreen />, this) }
                { this.renderingComponent(<StartingBuildScreen />, this) }
                { this.renderingComponent(<TurnOffLightScreen />, this) }
                { this.renderingComponent(<TakePhotoScreen />, this) }
            </div>
        )
    }

    returnControllingSequence() {
        return (
            <div>
                { this.renderingComponent(<DirtyHandsScreen />, this) }
                { this.renderingComponent(<MusicAdminScreen />, this) }
                { this.renderingComponent(<BookReaderScreen />, this) }
                { this.renderingComponent(<ShowingPresentationScreen />, this) }
                { this.renderingComponent(<SmartGlassesScreen />, this) }
                { this.renderingComponent(<IFTTTIntegrationScreen />, this) }
                { this.renderingComponent(<StartingBuildScreen />, this) }
                { this.renderingComponent(<TurnOffLightScreen />, this) }
                { this.renderingComponent(<TakePhotoScreen />, this) }
                { this.renderingComponent(<MonitorProductivityScreen />, this) }
                { this.renderingComponent(<MonitorStressScreen />, this) }
                { this.renderingComponent(<EyeCureScreen />, this) }
                { this.renderingComponent(<MonitorMoodScreen />, this) }
                { this.renderingComponent(<MonitorTicsScreen />, this) }
                { this.renderingComponent(<CountSmilesScreen />, this) }
                { this.renderingComponent(<CountBitesAndSipsScreen />, this) }
            </div>
        )
    }

    episodesSequence() {
        let link = window.location.href;
        let parameters = link.split("?");
        console.log(parameters);

        if (parameters.length > 1) {
            parameters = parameters[1].split("&");
            let type = "";

            for (let i = 0; i < parameters.length; i++) {
                let parameter = parameters[i];
                let value_array = parameter.split("=");

                if (value_array[0] === 'usecases') {
                    type = value_array[1];
                }

                break;
            }

            console.log(type);

            if (type === 'health') {
                return this.returnHealthSequence()
            }

            else if (type === 'controlling') {
                return this.returnControllingSequence()
            }

            else {
                return this.returnDefaultSequence()
            }
        }

        else {
            return this.returnDefaultSequence()
        }
    }

    render() {
        return (
            <div className="usecases">
                <ButtonLink
                    is_previous={true}
                    id={this.button_id__previous}
                    text={strings.usecases__prev} />

                { this.episodesSequence() }

                <ButtonLink
                    is_next={true}
                    id={this.button_id__next}
                    text={strings.usecases__next} />
            </div>
        )
    }
}