import React, { Component} from 'react';
import strings from '../Localisation';
import { IncomingTextMessage } from '../messages/TextMessages/IncomingTextMessage';
import { Category } from '../Category';
import {OutcomingTextMessage} from "../messages/TextMessages/OutcomingTextMessage";
import {IncomingGifMessage} from "../messages/GifMessages/IncomingGifMessage";
import {sendAmplitudeData} from "../../utils/amplitude";

export class CountBitesAndSipsScreen extends Component {
    constructor(props) {
        super(props);
        sendAmplitudeData("Usecases: viewed CountBitesAndSipsScreen")
    }

    render() {
        return (
            <Category
                title={strings.count_bites__title}
                subtitle={strings.count_bites__subtitle}>

                <IncomingTextMessage
                    text={strings.count_bites__app}
                    sender={strings.app} />

                <OutcomingTextMessage
                    text={strings.count_bites__user}
                    sender={strings.user}/>

                <IncomingGifMessage
                    path="/images/gifs/monitor_food.gif"
                    sender={strings.app}/>

            </Category>
        )
    }
}