import React, { Component} from 'react';
import { ButtonLink } from '../interactive/ButtonLink';
import strings from '../Localisation';
import idGenerator from 'react-id-generator';
import cookie from 'react-cookies'
import { scroller } from 'react-scroll'
import {Button} from "../interactive/Button";
import {sendAmplitudeData} from "../../utils/amplitude";

export class IntroScreen extends Component {
    constructor(props) {
        super(props);
        let language = cookie.load("language");
        language = language != null? language : "ru";

        this.state = {language: language};
        this.changeLanguage({}, language);

        this.button_id__en = idGenerator();
        this.button_id__ru = idGenerator();
        this.button_id__swipe = idGenerator();
    }

    componentDidMount() {
        let button__en = document.getElementById(this.button_id__en);
        let button__ru = document.getElementById(this.button_id__ru);
        let button__swipe = document.getElementById(this.button_id__swipe);
        let link = this;

        button__en.addEventListener("click", function(event) {
            link.logLanguage("en");
            link.changeLanguage(event, "en");
        });

        button__ru.addEventListener("click", function(event) {
            link.logLanguage("ru");
            link.changeLanguage(event, "ru");
        });

        button__swipe.addEventListener("click", function(event) {
            sendAmplitudeData("IntroScreen: swiped to email" );
            event.preventDefault();
            link.swipeDown();
        });

        this.checkUrlForSkipping(button__swipe);
    }

    logLanguage(lang) {
        sendAmplitudeData("IntroScreen: changed language on" + lang);
    }

    checkUrlForSkipping(button__swipe) {
        let link = window.location.href;
        let parameters = link.split("?");
        console.log(parameters);
        console.log(link);

        if (parameters.length > 1) {
            parameters = parameters[1].split("&");
            let type = "";

            for (let i = 0; i < parameters.length; i++) {
                let parameter = parameters[i];
                let value_array = parameter.split("=");

                if (value_array[0] === 'skip') {
                    type = value_array[1];
                }

                break;
            }

            console.log(type);

            if (type == "true") {
                button__swipe.click();
            }
        }
    }

    swipeDown(){
        scroller.scrollTo('category__email', {
            duration: 1500,
            delay: 100,
            smooth: true,
            offset: 50
        })
    }

    changeLanguage(e, lang) {
        try {
            e.preventDefault();
        }

        catch (e) {
            console.log(e);
        }

        strings.setLanguage(lang);

        console.log(strings.getLanguage());
        this.setState({language: strings.getLanguage()});
        cookie.save("language", strings.getLanguage());
        this.props.handle();
    }

    render() {
        return (
            <div className="intro category">
                <div className="intro__logo_langs">
                    <img className="intro__logo" src="/images/logonew.svg"/>

                    <div className="intro__langs_switch">
                        <ButtonLink
                            margin={true}
                            chosen={this.state.language === "en"}
                            text={strings.intro__en}
                            id={this.button_id__en}/>

                        <ButtonLink
                            margin={true}
                            chosen={this.state.language === "ru"}
                            text={strings.intro__ru}
                            id={this.button_id__ru}/>
                    </div>
                </div>

                <div className="intro__central">
                    <div className="intro_base__central">
                        <img className="intro__image" src="/images/eye.svg"/>

                        <div className="intro__text__first">
                            {strings.intro__text__first}
                        </div>
                        <div className="intro__text__second">
                            {strings.intro__text__second}
                        </div>

                        <Button
                            fullpage={true}
                            text={strings.intro__button}
                            id={this.button_id__swipe}/>
                    </div>
                </div>
            </div>
        )
    }
}