import React, { Component} from 'react';
let classNames = require('classnames');

export class TextMessage extends Component {
    render() {
        let class_name = classNames('message_text', {
            'message_content__incoming': this.props.is_incoming,
            'message_content__outcoming': !this.props.is_incoming
        });

        return (
            <div className={class_name}>
                {this.props.text}
            </div>
        )
    }
}