import React, { Component } from 'react';
let classNames = require('classnames');

export class ButtonLink extends Component {

    render() {
        let class_name = classNames('button_link', {
                'button_link__margin': this.props.margin,
                'button_link__chosen': this.props.chosen,
                'button_link__margin_left': this.props.margin_left,
                'button_link__top13': this.props.top13,
                'button_link__previous': this.props.is_previous,
                'button_link__next': this.props.is_next,
            }
        );

        return (
            <div className={class_name} id={this.props.id}>
                {this.props.text}
            </div>
        );
    }
}