import React, { Component} from 'react';
import { GifMessage} from './GifMessage';

export class IncomingGifMessage extends Component {
    render() {
        return (
            <div className="message__incoming message">
                {
                    this.props.sender?
                        <div className="message__sender">
                            {this.props.sender}
                        </div>
                        :
                        <div></div>
                }

                <GifMessage
                    is_incoming={true}
                    path={this.props.path} />
            </div>
        )
    }
}