import React, { Component } from 'react';
let classNames = require('classnames');

export class VideoMessage extends Component {

    render() {
        let class_name = classNames('message_video', {
            'message_content__incoming': this.props.is_incoming,
            'message_content__outcoming': !this.props.is_incoming
        });

        return (
            <iframe className={class_name} src={this.props.path} frameBorder="0" allowFullScreen />
        );
    }
}