import React, { Component} from 'react';
import strings from '../Localisation';
import { IncomingTextMessage } from '../messages/TextMessages/IncomingTextMessage';
import { Category } from '../Category';
import {OutcomingTextMessage} from "../messages/TextMessages/OutcomingTextMessage";
import {IncomingGifMessage} from "../messages/GifMessages/IncomingGifMessage";
import {sendAmplitudeData} from "../../utils/amplitude";

export class StartingBuildScreen extends Component {
    constructor(props) {
        super(props);
        sendAmplitudeData("Usecases: viewed StartingBuildScreen")
    }

    render() {
        return (
            <Category
                title={strings.starting_build__title}
                subtitle={strings.starting_build__subtitle}>

                <IncomingTextMessage
                    text={strings.starting_build__computer}
                    sender={strings.computer} />

                <OutcomingTextMessage
                    text={strings.starting_build__user}
                    sender={strings.user}/>

                <IncomingGifMessage
                    path="/images/gifs/scroll_with_eyes.gif"
                    sender={strings.app}/>

            </Category>
        )
    }
}