import React, { Component} from 'react';
import strings from '../Localisation';
import { IncomingTextMessage } from '../messages/TextMessages/IncomingTextMessage';
import { Category } from '../Category';
import {OutcomingTextMessage} from "../messages/TextMessages/OutcomingTextMessage";
import {IncomingVideoMessage} from "../messages/VideoMessages/IncomingVideoMessage";
import {sendAmplitudeData} from "../../utils/amplitude";

export class EyeCureScreen extends Component {
    constructor(props) {
        super(props);

        sendAmplitudeData("Usecases: viewed EyeCureScreen")
    }

    render() {
        return (
            <Category
                title={strings.eye_cure__title}
                subtitle={strings.eye_cure__subtitle}>

                <IncomingTextMessage
                    text={strings.eye_cure__app}
                    sender={strings.app} />

                <OutcomingTextMessage
                    text={strings.eye_cure__user}
                    sender={strings.user}/>

                <IncomingVideoMessage
                    path={strings.eye_cure__video_path}/>

            </Category>
        )
    }
}