import React, { Component} from 'react';
import strings from '../Localisation';
import { Category } from '../Category';
import {LinkMessage} from "../messages/LinkMessage";
import {MediaScreenshotMessage} from "../messages/PhotoMessages/MediaScreenshotMessage";
import { sendAmplitudeData } from '../../utils/amplitude';

export class CodeScreen extends Component {
    constructor(props) {
        super(props);
        sendAmplitudeData("Media: viewed CodeScreen")
    }

    render() {
        return (
            <Category
                title={strings.media_screen__title}
                subtitle={strings.media_screen__code}>

                <MediaScreenshotMessage
                    sender={strings.developer}
                    path={"images/media/code.png"}/>

                <LinkMessage
                    is_incoming={true}
                    text={strings.media_screen_link__code}
                    link={strings.media_screen_link__code} />

            </Category>
        )
    }
}