import React, { Component} from 'react';
import strings from '../Localisation';
import { IncomingTextMessage } from '../messages/TextMessages/IncomingTextMessage';
import { Category } from '../Category';
import {OutcomingTextMessage} from "../messages/TextMessages/OutcomingTextMessage";
import {IncomingGifMessage} from "../messages/GifMessages/IncomingGifMessage";
import {sendAmplitudeData} from "../../utils/amplitude";

export class TurnOffLightScreen extends Component {
    constructor(props) {
        super(props);

        sendAmplitudeData("Usecases: viewed TurnOffLightScreen")
    }

    render() {
        return (
            <Category
                title={strings.turn_off_light__title}
                subtitle={strings.turn_off_light__subtitle}>

                <IncomingTextMessage
                    text={strings.turn_off_light__light}
                    sender={strings.light} />

                <OutcomingTextMessage
                    text={strings.turn_off_light__user}
                    sender={strings.user}/>

                <IncomingGifMessage
                    path="/images/gifs/smart_house.gif"
                    sender={strings.app}/>

            </Category>
        )
    }
}