import React, { Component} from 'react';
import strings from '../Localisation';
import { IncomingTextMessage } from '../messages/TextMessages/IncomingTextMessage';
import { Category } from '../Category';
import {OutcomingTextMessage} from "../messages/TextMessages/OutcomingTextMessage";
import {IncomingGifMessage} from "../messages/GifMessages/IncomingGifMessage";
import {sendAmplitudeData} from "../../utils/amplitude";

export class MonitorStressScreen extends Component {
    constructor(props) {
        super(props);
        sendAmplitudeData("Usecases: viewed MonitorStressScreen")
    }

    render() {
        return (
            <Category
                title={strings.monitor_stress__title}
                subtitle={strings.monitor_stress__subtitle}>

                <IncomingTextMessage
                    text={strings.monitor_stress__app}
                    sender={strings.app} />

                <OutcomingTextMessage
                    text={strings.monitor_stress__user}
                    sender={strings.user}/>

                <IncomingGifMessage
                    path="/images/gifs/monitor_mood.gif"
                    sender={strings.app}/>

            </Category>
        )
    }
}