import React, { Component} from 'react';
import strings from '../Localisation';
import { IncomingTextMessage } from '../messages/TextMessages/IncomingTextMessage';
import { Category } from '../Category';
import {OutcomingTextMessage} from "../messages/TextMessages/OutcomingTextMessage";
import {IncomingGifMessage} from "../messages/GifMessages/IncomingGifMessage";
import {sendAmplitudeData} from "../../utils/amplitude";

export class ShowingPresentationScreen extends Component {
    constructor(props) {
        super(props);
        sendAmplitudeData("Usecases: viewed ShowingPresentationScreen")
    }

    render() {
        return (
            <Category
                title={strings.showing_presentation__title}
                subtitle={strings.showing_presentation__subtitle}>

                <IncomingTextMessage
                    text={strings.showing_presentation__presentation}
                    sender={strings.presentation} />

                <OutcomingTextMessage
                    text={strings.showing_presentation__user}
                    sender={strings.user}/>

                <IncomingGifMessage
                    path="/images/gifs/scroll_with_eyes.gif"
                    sender={strings.app}/>

            </Category>
        )
    }
}