import React, { Component} from 'react';
import {Category} from "../../Category";

export class AppScreenshotMessage extends Component {
    render() {
        return (
            <Category
                title={this.props.title}
                subtitle={this.props.subtitle}>

                <div className="app_screenshot">
                    {
                        this.props.sender?
                            <div className="message__sender">
                                {this.props.sender}
                            </div>
                            :
                            <div></div>
                    }

                    <img className="app_screenshot__img" src={this.props.path} />

                    {
                        this.props.sender?
                            <div className="message__sender">
                                {this.props.description}
                            </div>
                            :
                            <div></div>
                    }
                </div>
            </Category>
        )
    }
}