import React, { Component} from 'react';
import strings from '../Localisation';
import { IncomingTextMessage } from '../messages/TextMessages/IncomingTextMessage';
import { Category } from '../Category';
import {OutcomingTextMessage} from "../messages/TextMessages/OutcomingTextMessage";
import {IncomingGifMessage} from "../messages/GifMessages/IncomingGifMessage";
import {sendAmplitudeData} from "../../utils/amplitude";

export class SmartGlassesScreen extends Component {
    constructor(props) {
        super(props);
        sendAmplitudeData("Usecases: viewed SmartGlassesScreen")
    }

    render() {
        return (
            <Category
                title={strings.smart_glasses__title}
                subtitle={strings.smart_glasses__subtitle}>

                <IncomingTextMessage
                    text={strings.smart_glasses__list_first}
                    sender={strings.list} />

                <OutcomingTextMessage
                    text={strings.smart_glasses__user}
                    sender={strings.user}/>

                <IncomingTextMessage
                    text={strings.smart_glasses__list_second}
                    sender={strings.list}/>

                <IncomingGifMessage
                    path="/images/gifs/scroll_with_eyes.gif"
                    sender={strings.app}/>

            </Category>
        )
    }
}