import React, { Component} from 'react';
import strings from '../Localisation';
import { Category } from '../Category';
import {LinkMessage} from "../messages/LinkMessage";
import {MediaScreenshotMessage} from "../messages/PhotoMessages/MediaScreenshotMessage";
import { sendAmplitudeData } from '../../utils/amplitude';
import {IncomingTextMessage} from "../messages/TextMessages/IncomingTextMessage";

export class ShirtEyeMerch extends Component {

    constructor(props) {
        super(props);
        sendAmplitudeData("Media: viewed ShirtEyeMerch")
    }

    render() {
        return (
            <Category
                title={strings.merch_screen__title}
                subtitle={strings.merch_screen__eye_shirt}>

                <MediaScreenshotMessage
                    sender={strings.developer}
                    path={"images/merch/shirt1.png"}/>

                <IncomingTextMessage
                    text={strings.merch_screen__button_open_form} />

                <LinkMessage
                    is_incoming={true}
                    text={strings.merch_screen__form_url}
                    link={strings.merch_screen__form_url} />

            </Category>
        )
    }
}