import React, { Component} from 'react';
import strings from '../Localisation';
import { IncomingTextMessage } from '../messages/TextMessages/IncomingTextMessage';
import { Category } from '../Category';
import {OutcomingTextMessage} from "../messages/TextMessages/OutcomingTextMessage";
import {IncomingGifMessage} from "../messages/GifMessages/IncomingGifMessage";
import {sendAmplitudeData} from "../../utils/amplitude";

export class TakePhotoScreen extends Component {
    constructor(props) {
        super(props);

        sendAmplitudeData("Usecases: viewed TakePhotoScreen")
    }

    render() {
        return (
            <Category
                title={strings.take_photo__title}
                subtitle={strings.take_photo__subtitle}>

                <IncomingTextMessage
                    text={strings.take_photo__phone}
                    sender={strings.phone} />

                <OutcomingTextMessage
                    text={strings.take_photo__user}
                    sender={strings.user}/>

                <IncomingGifMessage
                    path="/images/gifs/take_photos.gif"
                    sender={strings.app}/>

            </Category>
        )
    }
}