import React, { Component} from 'react';
import strings from '../Localisation';
import { Category } from '../Category';
import {LinkMessage} from "../messages/LinkMessage";
import {MediaScreenshotMessage} from "../messages/PhotoMessages/MediaScreenshotMessage";
import { sendAmplitudeData } from '../../utils/amplitude';

export class RozetkedScreen extends Component {
    constructor(props) {
        super(props);
        sendAmplitudeData("Media: viewed RozetkedScreen")
    }

    render() {
        return (
            <Category
                title={strings.media_screen__title}
                subtitle={strings.media_screen__rozetked}>

                <MediaScreenshotMessage
                    sender={strings.developer}
                    path={"images/media/rozetked.png"}/>

                <LinkMessage
                    is_incoming={true}
                    text={strings.media_screen_link__rozetked}
                    link={strings.media_screen_link__rozetked} />

            </Category>
        )
    }
}