import React, { Component} from 'react';
import strings from "../Localisation";
import idGenerator from "react-id-generator";
import {ButtonLink} from "../interactive/ButtonLink";
import {AppScreenshotMessage} from "../messages/PhotoMessages/AppScreenshotMessage";
import {sendAmplitudeData} from "../../utils/amplitude";

export class AppScreen extends Component {
    local_index = 0;

    constructor(props) {
        super(props);
        this.state = {};

        this.count = 12;
        this.index = 0;
        this.local_index = 0;

        this.button_id__previous = idGenerator();
        this.button_id__next = idGenerator();
    }

    componentDidMount() {
        let button_previous = document.getElementById(this.button_id__previous);
        let button_next = document.getElementById(this.button_id__next);
        let link = this;

        button_previous.addEventListener("click", function(event) {
            event.preventDefault();

            link.index -= 1;
            if (link.index < 0) {
                link.index = link.count + link.index;
            }

            link.logClick();
            link.setState({});
        });

        button_next.addEventListener("click", function(event) {
            event.preventDefault();

            link.index += 1;

            link.logClick();
            link.setState({});
        });
    }

    logClick() {
        sendAmplitudeData("AppScreen: viewed screenshot number " + this.index);
    }

    renderingComponent(component, context) {
        context.index = context.index % context.count;
        context.local_index = context.local_index % context.count;

        console.log(context.index, context.count, context.local_index);

        const is_current = context.index === context.local_index;
        context.local_index += 1;

        return is_current? component : <div></div>;
    }

    returnDefaultSequence() {
        return (
                <div>
                    { this.renderingComponent(
                        <AppScreenshotMessage
                            title={strings.app_screenshot__title_create1}
                            subtitle={strings.app_screenshot__subtitle_create1}
                            path={"images/app/create1.jpg"}/>, this)
                    }
                    { this.renderingComponent(
                        <AppScreenshotMessage
                            title={strings.app_screenshot__title_create2}
                            subtitle={strings.app_screenshot__subtitle_create2}
                            path={"images/app/create2.jpg"}/>, this)
                    }
                    { this.renderingComponent(
                        <AppScreenshotMessage
                            title={strings.app_screenshot__title_create3}
                            subtitle={strings.app_screenshot__subtitle_create3}
                            path={"images/app/create3.jpg"}/>, this)
                    }
                    { this.renderingComponent(
                        <AppScreenshotMessage
                            title={strings.app_screenshot__title_gesture1}
                            subtitle={strings.app_screenshot__subtitle_gesture1}
                            path={"images/app/gesture1.jpg"}/>, this)
                    }
                    { this.renderingComponent(
                        <AppScreenshotMessage
                            title={strings.app_screenshot__title_gesture2}
                            subtitle={strings.app_screenshot__subtitle_gesture2}
                            path={"images/app/gesture2.jpg"}/>, this)
                    }
                    { this.renderingComponent(
                        <AppScreenshotMessage
                            title={strings.app_screenshot__title_gesture3}
                            subtitle={strings.app_screenshot__subtitle_gesture3}
                            path={"images/app/gesture3.jpg"}/>, this)
                    }
                    { this.renderingComponent(
                        <AppScreenshotMessage
                            title={strings.app_screenshot__title_gesture4}
                            subtitle={strings.app_screenshot__subtitle_gesture4}
                            path={"images/app/gesture4.jpg"}/>, this)
                    }
                    { this.renderingComponent(
                        <AppScreenshotMessage
                            title={strings.app_screenshot__title_gesture5}
                            subtitle={strings.app_screenshot__subtitle_gesture5}
                            path={"images/app/gesture5.jpg"}/>, this)
                    }
                    { this.renderingComponent(
                        <AppScreenshotMessage
                            title={strings.app_screenshot__title_gesture6}
                            subtitle={strings.app_screenshot__subtitle_gesture6}
                            path={"images/app/gesture6.jpg"}/>, this)
                    }
                    { this.renderingComponent(
                        <AppScreenshotMessage
                            title={strings.app_screenshot__title_gesture7}
                            subtitle={strings.app_screenshot__subtitle_gesture7}
                            path={"images/app/gesture7.jpg"}/>, this)
                    }
                    { this.renderingComponent(
                        <AppScreenshotMessage
                            title={strings.app_screenshot__title_gesture8}
                            subtitle={strings.app_screenshot__subtitle_gesture8}
                            path={"images/app/gesture8.jpg"}/>, this)
                    }
                    { this.renderingComponent(
                        <AppScreenshotMessage
                            title={strings.app_screenshot__title_gesture9}
                            subtitle={strings.app_screenshot__subtitle_gesture9}
                            path={"images/app/gesture9.jpg"}/>, this)
                    }
                </div>
        )
    }

    render() {
        return (
            <div className="usecases">
                <ButtonLink
                    is_previous={true}
                    id={this.button_id__previous}
                    text={strings.app_screenshot__prev} />

                { this.returnDefaultSequence() }

                <ButtonLink
                    is_next={true}
                    id={this.button_id__next}
                    text={strings.app_screenshot__next} />
            </div>
        )
    }
}