import React, { Component} from 'react';
import { sendAmplitudeData } from '../../utils/amplitude';
let classNames = require('classnames');

export class LinkMessage extends Component {
    constructor(props) {
        super(props);

        const classname = document.getElementsByClassName("message_link");
        for (let link of classname) {
            link.addEventListener('click', function(e) {
                const href = e.target.closest('a').href || '';
                sendAmplitudeData("LinkMessage: clicked on " + href);
            }, false);
        }
    }
    render() {
        let class_name = classNames('message_text message', {
            'message__incoming': this.props.is_incoming,
            'message_content__incoming': this.props.is_incoming,
            'message__outcoming': !this.props.is_incoming,
            'message_content__outcoming': !this.props.is_incoming,
            'last_thing': this.props.is_last
        });

        return (
            <div className={class_name}>
                <div><a className="message_link" href={this.props.link}>{this.props.text}</a></div>
            </div>
        )
    }
}