import React, { Component} from 'react';
let classNames = require('classnames');

export class GifMessage extends Component {
    render() {
        let class_name = classNames('message_gif', {
            'message_content__incoming': this.props.is_incoming,
            'message_content__outcoming': !this.props.is_incoming
        });

        return (
            <img className={class_name} src={this.props.path} />
        )
    }
}